import React from 'react'

const MainAds = () => (
  <div className="col-md-12 headline">
    <div className="bar-view widget">
      <div className="inner clearfix">
        <div className="col-md-2 bar-title" style={{lineHeight: '100px', height: 132}}>
          <div className="title-icon">
            <i className="fa fa-bookmark-o"></i>
          </div>
          <span className="title">Advertisement</span>
        </div>
        <div className="col-md-10 bar-content bar-link">
          <img src="img/banner_728.png" alt="Main Ads" />
        </div>
      </div>
    </div>
  </div>
)

export default MainAds



